@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --menu-button-open: url("data:image/svg+xml,%3Csvg width='24' height='20' viewBox='0 0 24 20' fill='%2381B3D2' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24 0H0v4h24V0Zm0 8H0v4h24V8Zm0 8H0v4h24v-4Z'/%3E%3C/svg%3E");
  --menu-button-close: url("data:image/svg+xml,%3Csvg width='26' height='25' viewBox='0 0 26 25' fill='%23fff' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M26 3.153 22.72 0 13 9.347 3.28 0 0 3.153 9.72 12.5 0 21.847 3.28 25 13 15.653 22.72 25 26 21.847 16.28 12.5 26 3.153Z'/%3E%3C/svg%3E");
  --hero-triangle: url("data:image/svg+xml,%3Csvg width='1200' height='57' viewBox='0 0 1200 57' fill='%23fff' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M249.62 23.7 0 0v57h600.05L249.62 0v23.7Zm700.86 0V0L600.05 57H1200V0L950.48 23.7Z'/%3E%3C/svg%3E");
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  background-color: #f2f2f2;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.logo {
  display: inline-block;
}

.logo__image {
  display: block;
}

.logo:hover .logo__image,
.logo:focus-visible .logo__image {
  filter: opacity(0.8);
}

.logo:focus-visible {
  outline: none;
}

.logo:active .logo__image {
  filter: opacity(0.5);
}

.main-navigation {
  background-color: #81b3d3;
}

.main-navigation__list {
  padding: 0;
  margin: 0;
  list-style: none;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.2) 0, rgba(0, 0, 0, 0) 7px);
}

.main-navigation__list--closed {
  display: none;
}

.main-navigation__list--closed + .main-navigation__toggle-button {
  background-image: var(--menu-button-open);
}

.main-navigation__item {
  text-align: center;
  border-bottom: 1px solid #678fa8;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
}
.main-navigation__item:last-child {
  border-bottom: none;
}

.main-navigation__link {
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  display: block;
  padding: 12px;
  text-decoration: none;
  cursor: pointer;
}
.main-navigation__link:hover, .main-navigation__link:focus-visible {
  outline: none;
  color: rgba(255, 255, 255, 0.6);
}
.main-navigation__link:active {
  color: rgba(255, 255, 255, 0.3);
}

.main-navigation__toggle-button {
  position: absolute;
  top: 0;
  right: 6px;
  display: block;
  padding: 0;
  width: 50px;
  height: 50px;
  background: none;
  border: none;
  background-image: var(--menu-button-close);
  background-position: center;
  background-repeat: no-repeat;
}

.main-navigation__toggle-button:is(:hover, :focus-visible) {
  opacity: 0.6;
}

.main-navigation__toggle-button:focus-visible {
  outline: none;
}

.main-navigation__toggle-button:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-navigation {
    background-color: transparent;
  }

  .main-navigation__toggle-button {
    display: none;
  }

  .main-navigation__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-flow: dense;
    background-image: none;
  }
  .main-navigation__list::before {
    content: "";
    display: block;
    grid-column: 3;
    grid-row: span 2;
  }

  .main-navigation__item {
    box-shadow: none;
    border: none;
  }

  .main-navigation__link {
    padding-top: 18px;
    padding-bottom: 17px;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
  }
  .main-navigation__link:hover, .main-navigation__link:focus-visible {
    color: #81b3d3;
    outline: none;
  }
  .main-navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }

  .main-navigation__link--current,
.main-navigation__link--current:hover,
.main-navigation__link--current:active {
    color: #766357;
  }
}
.main-header {
  position: relative;
  min-height: 56px;
}

.main-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.main-header__navigation {
  position: absolute;
  top: 0;
  width: 100%;
}

@media (min-width: 768px) {
  .main-header__navigation {
    position: static;
  }
}
.html-academy-logo {
  display: block;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
  text-decoration: none;
}
.html-academy-logo:hover, .html-academy-logo:focus-visible {
  outline: none;
  color: #81b3d3;
}
.html-academy-logo:active {
  color: rgba(0, 0, 0, 0.3);
}

.html-academy-logo__image {
  margin-inline-start: 10px;
  vertical-align: middle;
}

.social-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: #81b3d3;
  fill: #ffffff;
}
.social-button:is(:hover, :focus-visible) {
  background-color: #669ec0;
}
.social-button:focus-visible {
  outline: none;
}
.social-button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.social-button:active .social-button__icon {
  opacity: 0.3;
}
.social-button--disabled, .social-button--disabled:hover, .social-button--disabled:focus-visible, .social-button--disabled:active {
  color: #ffffff;
  background-color: #e5e5e5;
}

.socials {
  display: flex;
  justify-content: center;
  gap: 6px;
  padding: 0;
  list-style-type: none;
}

.main-footer {
  padding-top: 40px;
  padding-right: 20px;
  padding-bottom: 23px;
  padding-left: 20px;
}

.main-footer__hashtag {
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
  margin-bottom: 34px;
  text-align: center;
}

.main-footer__socials-list {
  margin: 0 auto;
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  .main-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: 36px;
    padding-inline: 60px;
  }

  .main-footer__hashtag {
    margin: 0;
  }

  .main-footer__socials-list {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .main-footer {
    padding-inline: 120px;
  }
}
.page__hero {
  margin-bottom: 32px;
}

.page__title {
  margin: 0 auto;
  margin-bottom: 35px;
  max-width: 500px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 18px;
  line-height: 26px;
