  margin-bottom: 28px;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
}

@media (min-width: 768px) {
  .page__hero {
    margin-bottom: 47px;
  }

  .page__title {
    font-size: 30px;
    line-height: 40px;
  }

  .page__title--index {
