    margin-bottom: 56px;
  }
}
.modal {
  position: fixed;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal--closed {
  display: none;
}

.modal__content {
  background-color: #ffffff;
  max-width: 400px;
}

.modal--wide .modal__content {
  max-width: 640px;
}

.modal__text {
  padding: 45px;
  padding-bottom: 30px;
}

.modal__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
}

.modal__description {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
}

.modal__button-wrapper {
  padding-bottom: 35px;
}

.modal__button {
  width: 180px;
  margin: 0 auto;
}

.modal__button-wrapper--visible {
  padding: 20px;
  background-color: #f2f2f2;
}
.modal__button-wrapper--visible .modal__button {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}